.list-container {
    position: relative;
    margin: auto;
    height: 82vh;
    width: 50vw;
    border-radius: 22px;
    box-shadow: 0px 50px 50px rgba(0, 0, 0, 0.15);
    background: linear-gradient(209.22deg, #ffffff 4.25%, #f1f1f1 95.6%);
    padding: 1rem 1rem 0 1rem;
    display: flex;
    flex-direction: column;
}

.list-container h1 {
    font-weight: 300;
    text-transform: uppercase;
    margin: 0;
    padding: 1rem 0;
}

.list-container form {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 280px;
    margin: 0 auto;
}

.list-container input[type="text"] {
    border: solid 1px var(--border-light);
    outline: none;
    font-size: 1.2rem;
    color: #666;
    background: none;
    text-align: center;
    width: auto;
    border-radius: 8px;
    width: 100%;
    padding: 5px 1rem 8px 0;
}

.list-container form button {
    background: #fff;
    border: none;
    border-radius: 50%;
    box-shadow: 0 14px 26px -12px rgba(0,0,0,0.42), 0 4px 23px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0,0,0,0.2);
    color: var(--border-dark);
    font-weight: 300;
    font-size: 40px;
    height: 40px;
    width: 40px;
    outline: none;
    position: absolute;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    right: -1rem;
    transition: ease 0.2s;
}

.list-container form button:active {
    transform: scale(0.90);
    box-shadow: 0 14px 26px -12px rgba(0,0,0,0.22), 0 4px 23px 0px rgba(0, 0, 0, 0.1), 0 8px 10px -5px rgba(0,0,0,0.1);
}

.list-container form button svg {
    height: 40px;
    fill: #F00;
    color: #F00;
}


.list ul {
    list-style: none;
    margin: 2rem 0 0 0;
    padding: 0;
}

.list {
    margin-top: 1rem;
    overflow-y: scroll;
}

.list li {
    position: relative;
    padding: 0.5rem 0;
    max-width: 60%;
    margin: 0 auto;
    border-bottom: 1px solid var(--border-light);
}

.list li button {
    position: absolute;
    right: 0;
    width: 18px;
    height: 18px;
    background: none;
    border: solid 2px var(--border-light);
    border-radius: 50%;
    outline: none;
}

.list li button:hover {
    background: #00c400;
    border-color: #00c400;
}


@media screen and (max-width:780px) {
    .list-container {
        width: 80vw;
    }

    .list-container h1 {
        font-size: 1.5rem;
    }

    .list li {
        max-width: 100%;
        font-size: 0.8rem;
    }


}
