.App {
  text-align: center;
  padding-bottom: 2rem;
}

.App header {
    display: flex;
    justify-content: flex-end;
}

.App header button {
    background: none;
    border: none;
    width: 2.5rem;
    height: 2.5rem;
    padding: 0;
    margin: 0.5rem 1rem;
    color: #666;
}

button:hover {
    cursor: pointer;
}
